.Auth_wrapper {
    height: 100vh;
    padding: 5vh 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    background: 
        repeating-linear-gradient(
            135deg,
            var(--color2),
            var(--color2) .2em,
            transparent .2em,
            transparent 1.6em
        ),
        -webkit-gradient(
            linear,
            left top, left bottom,
            from(var(--color2)),
            to(var(--black))
        );
    background: 
        -webkit-repeating-linear-gradient(
            315deg,
            var(--color2),
            var(--color2) .2em,
            transparent .2em,
            transparent 1.6em
        ),
        -webkit-linear-gradient(
            top,
            var(--color2),
            var(--black)
        );
    background: 
        -o-repeating-linear-gradient(
            315deg,
            var(--color2),
            var(--color2) .2em,
            transparent .2em,
            transparent 1.6em
        ),
        -o-linear-gradient(
            top,
            var(--color2),
            var(--black)
        );
    background: 
        repeating-linear-gradient(
            135deg,
            var(--color2),
            var(--color2) .2em,
            transparent .2em,
            transparent 1.6em
        ),
        linear-gradient(
            to bottom,
            var(--color2),
            var(--black)
        );
}

.Auth_info {
    color: var(--white);
    font-size: 1.6rem;
    padding: 0 1em 1em;
}

.Auth_button_wrapper {
    height: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.App_name {
    color: var(--white);
    font-size: calc(1.6rem + 2.2vmax);
    font-weight: 300;
    margin-bottom: 1em;
    padding: 0 1em;
}

.Auth_button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: calc(1.6rem + 2.2vmax);
    font-weight: 700;
    color: var(--color2);
    fill: var(--color2);
    -webkit-transition: color .2s ease-in-out;
    -o-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
    padding: 2em 0;
}
.Auth_button::after {
    content: '';
    background-color: var(--white);
    border: .2rem solid var(--color2);
    display: block;
    width: 6em;
    height: 6em;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
        -ms-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.Auth_icon {
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: block;
    min-height: calc(2rem + 2.2vmax);
    -webkit-transition: fill .2s ease-in-out;
    -o-transition: fill .2s ease-in-out;
    transition: fill .2s ease-in-out;
}

.Auth_button:hover {
    color: var(--color1);
    fill: var(--color1);
}



