* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*::after,
*::before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

:root {
  --white: #fefefe;
  --gray: #414141;
  --black: #232323;
  --color1: #00ffe6;
  --color2: #027c6d;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  width: 100%;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  background-color: var(--white);
  color: var(--gray);
  overflow-x: hidden;
  width: 100%;
}

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  outline: 0;
  position: relative;
  z-index: 2;
}

.Content_wrapper {
  min-height: 100vh;
  padding: 5vh 2rem;
  color: var(--white);
  background: 
      -webkit-gradient(
          linear,
          left top, left bottom,
          from(var(--color2)),
          to(var(--black))
      );
  background: 
      -webkit-linear-gradient(
          top,
          var(--color2),
          var(--black)
      );
  background: 
      -o-linear-gradient(
          top,
          var(--color2),
          var(--black)
      );
  background: 
      linear-gradient(
          to bottom,
          var(--color2),
          var(--black)
      );
}
.Content_wrapper > p {
  line-height: 1.5;
  font-weight: 300;
  font-size: 1.6rem;
  text-align: justify;
}

.Content_wrapper code {
  color: var(--color1);
  font-family: monospace;
  letter-spacing: .1em;
  font-size: 1.2em;
}

.Content_wrapper hr {
  margin: 2rem 0;
  border-color: var(--color1);
  position: relative;
}

.Basic_button {
  display: block;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: 0;
  color: var(--white);
  font-size: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  z-index: 2;
  margin: 1em auto;
}

.Show_all {
  font-size: 1.4rem;
  margin: -1em auto 1em;
}

.Show_all svg {
  display: inline-block;
  vertical-align: sub;
  height: 1.5em;
  fill: var(--white);
}

.About-link {
  color: var(--color2);
  font-size: 1.4rem;
  font-weight: 700;
  position: relative;
  z-index: 2;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: 1em 2em;
  text-decoration: none;
}
.About-link::after {
  content: '';
  background-color: var(--white);
  border: .2rem solid var(--color2);
  display: block;
  width: 2em;
  height: 2em;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
      -ms-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.Calendar_header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.Calendar_header h2 {
  font-size: calc(1.6rem + .8vmax);
  margin-bottom: .3em;
  max-width: 80%;
  word-break: break-all;
}

.Calendar_header .Basic_button {
  margin: 0;
}


